header .grid-area {
  display: grid;
  grid-template-areas: "c d e";
  align-items: center;
}
@media screen and (max-width: 768px) {
  header .grid-area {
    grid-template-areas: "d" "c";
  }
  header .grid-area .grid-e {
    display: none;
  }
}
header .grid-area .grid-a {
  grid-area: a;
}
header .grid-area .grid-b {
  grid-area: b;
}
header .grid-area .grid-c {
  grid-area: c;
  display: grid;
  grid-template-areas: "a b";
}
header .grid-area .grid-d {
  grid-area: d;
}
header .grid-area .grid-e {
  grid-area: e;
}/*# sourceMappingURL=header.css.map */